<template>
  <van-nav-bar title="" left-arrow @click-left="onClickLeft"/>
  <van-row>
    <van-col span="24">
      <img src="../../assets/images/register.png" alt="">
    </van-col>
  </van-row>
  <van-form @submit="sendData">
    <van-cell-group inset>
      <!--  pattern  -->
      <van-field
          v-model="email"
          name="email"
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
      />
      <!--  validator  -->
      <van-field
          v-model="password"
          name="password"
          label=""
          type="password"
          placeholder=""
          :rules="[{ required: true, message: '' }]"
      />
    </van-cell-group>
    <div class="overs">
      <router-link to="/register" class="tabLogin"></router-link>
      <van-button color="#f05549" round block type="primary" native-type="submit">
        
      </van-button>
    </div>
  </van-form>
</template>

<script>

import {reactive, toRefs} from "vue";
import {PostLogin} from "@/network/user";
import { Toast } from 'vant';
import {useRouter} from "vue-router";

export default {
  name: "UserSignIn",
  setup() {
    const onClickLeft = () => history.back();
    const data = reactive({
      email: '',
      password: '',
    })
    const route = useRouter();
    const sendData = (data)=>{
      PostLogin(data).then(res=>{
        window.localStorage.setItem('token',res.data.access_token);
        if(res.status === 200){
          Toast.success('!');
          route.push({name:'user'})
        }
      })
    }
    return {
      ...toRefs(data),
      sendData,
      onClickLeft,
    };
  },
}
</script>

<style scoped>
img{
  margin: 20px 0;
}
.overs{
  margin: 16px;
  text-align: left;
}
.tabLogin{
  margin-left: 10px;
  display: inline-block;
  margin-bottom: 10px;
  font-size: 14px;
  color: dodgerblue;
}
</style>