<template>
  <van-nav-bar title="" left-arrow @click-left="onClickLeft"/>
  <van-row>
    <van-col span="24">
      <img src="../../assets/images/register.png" alt="">
    </van-col>
  </van-row>
  <van-form @submit="sendData">
    <van-cell-group inset>
      <!--  pattern  -->
      <van-field
          v-model="name"
          name="name"
          label=""
          placeholder=""
          :rules="[{ names, required: true, message: '' }]"
      />
      <!--  validator  -->
      <van-field
          v-model="password"
          name="password"
          label=""
          type="password"
          placeholder=""
          :rules="[{ passwords, required: true, message: '61111' }]"
      />
      <!--  validator  -->
      <van-field
          v-model="password_confirmation"
          name="password_confirmation"
          label=""
          type="password"
          placeholder=""
          :rules="[{ required: true, message: '' }]"
      />
      <!--  validator  -->
      <van-field
          v-model="email"
          name="email"
          label=""
          placeholder=""
          :rules="[{ emails, required: true, message: '' }]"
      />
      </van-cell-group>
    <div class="overs">
      <router-link to="/signIn" class="tabLogin"></router-link>
      <van-button color="#f05549" round block type="primary" native-type="submit">
        
      </van-button>
    </div>
  </van-form>
</template>

<script>
import {reactive, toRefs} from "vue";
import {PostRegister} from "@/network/user";
import { Toast } from 'vant';
import {useRouter} from "vue-router";

export default {
  name: "UserRegister",
  setup() {
    const onClickLeft = () => history.back();
    const data = reactive({
      name: '',
      email: '',
      password: '',
      password_confirmation: '',
    })
    const names = /^[a-zA-Z0-9_-]{4,16}$/;
    const passwords = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
    const emails = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;

    const route = useRouter();
    console.log(route);
    const sendData = ()=>{
      if(data.password !== data.password_confirmation){
        Toast.fail('!');
      }else {
        PostRegister(data).then(res=>{
          console.log(res);
          if(res.status === 201){
            Toast.success('!');
            setTimeout(() => {
              route.push({name:'signIn'})
            }, 1000);
          }
        })
      }
    }
    return {
      ...toRefs(data),
      names,
      passwords,
      emails,
      sendData,
      route,
      onClickLeft,
    };
  },
}
</script>

<style scoped>
img{
  margin: 20px 0;
}
.overs{
  margin: 16px;
  text-align: left;
}
.tabLogin{
  margin-left: 10px;
  display: inline-block;
  margin-bottom: 10px;
  font-size: 14px;
  color: dodgerblue;
}
</style>